
let totalCount = 0;
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

import { SeourceCreatedList } from "@/servers/api";
import Divider from "@/components/common/DividedArea";
export default {
name: "",
props: [""],
data() {
return {
dataList: [],
refreshText: "下拉刷新",
noDataText: "没有更多数据",
scrollbarObj: {
fade: true
},
pullDownRefreshObj: {
threshold: 70,
stop: 40
},
pullUpLoadObj: {
threshold: 0,
txt: {
more: "加载更多",
noMore: "没有更多数据了"
}
},
startY: 45,
scrollToX: 0,
scrollToY: 0,
scrollToTime: 700,
items: [],
page: 1
};
},

components: {
InlineLoading,
Divider
},
filters: {
stringToArr(str) {
return str.split(",")[0];
}
},
computed: {},

beforeMount() {},

mounted() {
this.onPullingDown();
},

methods: {
getDetailsContent(id) {
let type = this.$route.query.branch;
this.$router.push("/imageandtextdetails?type=" + type + "&id=" + id);
},

scrollTo() {
this.$refs.scroll.scrollTo(
this.scrollToX,
this.scrollToY,
this.scrollToTime
);
},

getData() {
return new Promise(resolve => {
let arr = [];
SeourceCreatedList({
type: 10,
page: this.page
})
.then(res => {
if (res.result === 1) {
totalCount = res.data.totalofnum;
setTimeout(() => {
if (res.data.comment.length) {
arr = res.data.comment;
resolve(res.data.comment);
} else {
this.$refs.scroll.forceUpdate(false);
}
}, 1000);
}
})
.catch(err => {
console.log(err);
});
});
},
onPullingDown() {
totalCount = 0;
this.page = 1;
this.getData().then(res => {
this.dataList = res;
this.$nextTick(() => {
this.$refs.scroll.forceUpdate(true);
});
});
},
onPullingUp() {
this.page += 1;
this.getData().then(res => {
this.dataList = this.dataList.concat(res);
if (this.dataList.length < totalCount) {
this.$refs.scroll.forceUpdate(true);
} else {
this.$refs.scroll.forceUpdate(false);
}
});
},
back() {
this.$router.goBack();
},
releaseContent() {
this.$router.push("/releaseimgandtext");
}
},

watch: {}
};



